@import '../node_modules/bootstrap/dist/css/bootstrap.css';
 
 
body {
  background: #fff;
  color: #61657a;
  font-family: 'Karla', sans-serif;
  font-size: 15px;
  font-weight: normal;
  line-height: 26px;
  margin: 0;
  padding: 0;
  position: relative;
}
html, button, input, select, textarea {
   font-family: 'Karla', sans-serif;
}

img {
  max-width: 100%;
}

/* === PARAGRAPH === */

p {
  margin: 0 0 10px;
}

b,
strong {
  font-weight: 600;
}
 
/* ===  Headings === */

h1,
h2,
h3,
h4,
h5,
h6 {
  color: #2b343b;
  font-family: 'Playfair Display', sans-serif;
  margin: 0 0 15px;
  padding: 0;
  transition: 0.3s;
}

h1 {
  font-size: 36px;
  line-height: 52px;
  font-weight: 700;
}

h2 {
  font-size: 30px;
  line-height: 45px;
  font-weight: 700;
}

h3 {
  font-size: 28px;
  line-height: 32px;
  font-weight: 700;
}

h4 {
  font-size: 22px;
  line-height: 30px;
}

h5 {
  font-size: 20px;
  line-height: 28px;
}

h6 {
  font-size: 18px;
}

/* ## End: Headings ## */

/* === Hyperlink === */

a {
  color: #303030 ;
    /*transition*/
  -webkit-transition: all 0.3s ease-in-out;
  -moz-transition: all 0.3s ease-in-out;
  -o-transition: all 0.3s ease-in-out;
  transition: all 0.3s ease-in-out;
  text-decoration: none;
}

a,
a > * {
  cursor: pointer;
  outline: medium none;
  text-decoration: none;
}

a:focus,
a:hover {
  outline: medium none;
  text-decoration: none;
  color: inherit;
  text-decoration: none !important;
}

h1 a,
h2 a,
h3 a,
h4 a,
h5 a,
h6 a {
  color: inherit;
}

ul {
  padding: 0;
  margin: 0;
  list-style-type: none;
} 

/* === Hyperlink === */

.btn.btn-fill {
  background: #fff;
}

.btn.btn-fill {
  color: #000;
}

.btn {
  border-width: 1px;
  border-style: solid;
  -webkit-border-radius: 0px;
  -moz-border-radius: 0px;
  -ms-border-radius: 0px;
  -o-border-radius: 0px;
  border-radius: 0px;
  cursor: pointer;
  padding: 10px 22px;
  display: inline-block;
  text-transform: uppercase;
  letter-spacing: 2px;
  position: relative;
  -webkit-transition: all 0.4s ease-out 0s;
  -moz-transition: all 0.4s ease-out 0s;
  -ms-transition: all 0.4s ease-out 0s;
  -o-transition: all 0.4s ease-out 0s;
  transition: all 0.4s ease-out 0s;
  color: #fff;
}
 

.slides_wrapper .react-slideshow-container + div.indicators {
    margin-top: 0px;
    position: absolute;
    bottom: 0px;
    left: 50%;
    margin-left: -10px;
    margin-bottom: 30px;
}
 
.slides_wrapper .react-slideshow-container + div.indicators > div.active {
    width: 15px;
    height: 8px;
    border-radius: 7px;
    background: #9dca00;
}
.slides_wrapper .react-slideshow-container + div.indicators > div.active {
    background: #9dca00;
}
.slides_wrapper .react-slideshow-container + div.indicators > div {
    background: #2b343b;
}
.slides_wrapper .react-slideshow-container + div.indicators > div:hover {
    background: #9dca00;
}
.slides_wrapper .react-slideshow-container + div.indicators > div {
    width: 8px;
    height: 8px; 
}